.mySwiperImages{
  max-width: 1100px;
  margin-top: 0.6rem;
}
.productName{
  text-align: center;
  ;
}
.product-content {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-content{
  height: 500px;
}
.product-pagination-bullet-active{
  background-color: red;
}
.product-button-next {
  color: red;
}

.product-button-prev {
  color: red;
}

.product img {
  width: 100%;
  object-fit: cover;
  background-position: center;
}

.product img:hover{
  transform: scale(1.05);
  transition: 0.4s;
}

.product {
  width: 90%;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}




.product-button-next {
  position: absolute;
  top: 88%;
  left: 60%;
  padding-left: 8px;
  border-radius: 50%;
  width: 80px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    height: 60px;
}

.product-button-prev {
  border-radius: 50%;
  width: 80px;
  padding-right: 8px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;  position: absolute;
  top: 88%;
  left: 29%;
  height: 60px;
}



@media (max-width:1100px) {
  .product{
    height: 45vh;
  }

}

