@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap');



:root{
  --dark_red: rgb(228, 19, 19);

}
.productdisplay {
  display: flex;
  align-items: flex-start;
  overflow: auto;
}

.display-right {
  display: flex;
  flex-direction: column;

}

.title h1{
  text-transform: uppercase;
  
}
.bgImg img {
  max-height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: left;
}
.display-left{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.boxes{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 4rem 0;
  max-height: 450px;
}

.box{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  margin: 12px;
  width: 45%;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.819);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
cursor: pointer;
}

.box i{
  font-size: 30px;
  /* color: rgb(67, 64, 64); */
  color: white;
  padding-right: 10px;
}

.box p{
  font-size: 25px;
  /* color: rgb(63, 61, 61); */
  color: white;
  margin: 0;
  
}
.box-content{
  /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
  /* border-radius: 50%; */
  padding: 5px;
  width: 160px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* background-color: #ffffff; */
}
.box:hover{
background-color: #ffffff;
transition: 0.3s ease-in-out;

}
.box:hover .box-content{
  transition: 0.3s ease-in-out;

  }

.box:hover i, .box:hover p{
  color: var(--dark_red);
  transition: 0.3s ease-in-out;
}

@media (min-width: 992px) {
  .box {
    width: calc(50% - 24px); 
  }
}
@media (max-width: 528px) {
  .box {
    width: 92%;
  }
}
.display-content {
  padding: 3rem;
}

.title h1 {
  font-size: 25px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  width: 100%;
}

/* .bg-description hr {
  width: 85%;
  color: red;
  margin-left: 5rem;
} */

pre {
background-color: white;
  font-family: 'Lato', sans-serif;
  overflow-x: auto;
  padding-left: 2rem;
  font-size: 18px;
}

.description1{
  display: none;
}

.description h1 ,
.description1 h1{
  padding: 1rem 0 0.5rem 2rem;
  background-color: white;
}


pre::-webkit-scrollbar {
  width: 10px;
  height: 6px;
}

pre::-webkit-scrollbar-track {
  background: #f1f1f1;
}

pre::-webkit-scrollbar-thumb {
  background: #888;
}

pre::-webkit-scrollbar-thumb:hover {
  background: #555;
}


@media (max-width:500px) {
  /* .bg-description h1 {
    margin-top: 6rem;
    font-size: 30px;
    width: 70%;
  }

  .bg-description {
    margin: -3rem;
  } */
}

@media (max-width:992px) {

  .description1{
    display: block;
  }

  .description{
    display: none;
  }
  .title h1{
    margin-top: 1rem;
  }

  .bgImg img{
    width: 100%;
  }


  
}

.titles-product{
  margin: 3rem 1rem;
}
.titles-product h1{
  position: relative;
  color: rgb(43, 41, 41);
  margin-right: 50px;
}

.titles-product h1::after{
  background-color: rgba(228, 19, 19, 0.773);
  display: block;
  width: 70%;
  height: 3px;
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  z-index: -1;;
}
.titles-product h6{
  color: rgba(228, 19, 19, 0.773);
}

@media (max-width:785px) {
 
  
  
 
}
@media (max-width:767px) {
 
  
  .titles-product h1::after{
    width: 48%;
  }
 
}


@media (max-width:650px) {
 
  .titles-product h1{
    width: 100%;
  }
  .titles-product h1::after{
    width: 50%;
  }
 
}
@media (max-width:550px) {
 
  .titles-product h1{
    width: 100%;
  }
  .titles-product h1::after{
    width: 48%;
  }

  .about-me p,.about-me h2{
    margin: 0rem -1rem 1rem;
  }
  
}