  .learn-about{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: auto;
    font-family: 'Lato', sans-serif;
  
  }
  .about-img:hover img{
    transform: translateY(7px);
  }
  .about-me::-webkit-scrollbar {
    width: 10px;
    height: 6px;
  }
  
  .about-me::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  .about-me::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  .about-me::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  
  .about-me{
    flex-basis: 50%;
    color: rgb(39, 39, 39);
    padding-right: 2rem;
    font-family: 'Montserrat', sans-serif;
  }
  .about-me p{
    line-height: 2rem;
  }
  @media (max-width:993px) {
    .about-img{
      flex-basis: 50%;
      
    }
  }
  .about-me{
    flex-basis: 50%;
    overflow-y: scroll;
    max-height: 390px;

  }

  @media (max-width:767px) {
    .learn-about{
      width: 100%;
    }
    .about-img img{
      box-shadow: 0px 2px 30px 0px rgba(0, 19, 19, 0.173);
      flex-basis: 100%;
      width: 500px;
      margin: auto;
    }
    .about-me{
      flex-basis: 100%;
      width: 90%;
      margin: auto;
      margin-top: 2rem;
      overflow: hidden;
      padding-left: 1rem;
    }
    #about{
      margin-left: 4rem;
    }
    
    .titles-aboutMe h1{
      width: 100%;
    }
    .titles-aboutMe h1::after{
      display: none;
    }
   
  }


  @media (max-width:560px) {
    .about-img img{
      width: 350px;
    }

    
    #about{
      margin: 2rem;
    }
  }

  .titles-aboutMe{
    margin: 3rem 0rem;
  }
  .titles-aboutMe h1{
    position: relative;
    color: rgb(43, 41, 41);
    margin-right: 50px;
  }
  
  .titles-aboutMe h1::after{
    background-color: rgba(228, 19, 19, 0.773);
    display: block;
    width: 70%;
    height: 3px;
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    z-index: -1;;
  }
  .titles-aboutMe h6{
    color: rgba(228, 19, 19, 0.773);
  }
  
  
  @media (max-width:768px) {
   
    
    .titles-aboutMe h1::after{
      width: 48%;
    }
   
  }
  
  
  @media (max-width:650px) {
   
    .titles-aboutMe h1{
      width: 100%;
    }
    .titles-aboutMe h1::after{
      width: 50%;
    }
   
  }
  @media (max-width:550px) {
   
    .titles-aboutMe h1{
      width: 100%;
    }
    .titles-aboutMe h1::after{
      width: 42%;
    }

    .about-me p,.about-me h2{
      margin: 0rem -1rem 1rem;
    }
    
  }

  @media (max-width:450px) {
    .about-img img{
      width: 300px;
    }
  }

  @media (max-width:370px) {
    .about-img img{
      width: 230px;
    }
  }