
.swiper-content {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;

}
.bg-white{
  background-color: white;
}
.swiper-pagination-bullet-active{
  background-color: red;
}
.swiper-button-next ,.swiper-button-prev{
  color: white;
}

.swiper{
  display: flex;
  cursor: grab;
  background-color: whi;
}

.swiper img {
  width: 100%;
  object-fit: cover;
  background-position: center;
  position: relative;
  border-bottom: 1px solid #5c5c5c;
}

.swiper img:hover{
  transform: scale(1.02);
  transition: 0.4s;
  
}

.swiper {
  width: 90%;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bg-whitee a {
  text-align: center;
  width: 100%;
  background-color: rgb(234, 232, 232);
  padding: 5px 10px;
  z-index: 1;
  position: relative;
  font-size: medium;

}




.swiper-button-next,
.swiper-button-prev {
  background-color: white;
  background-color: rgba(0, 0, 0, 0.8555);
  right: 0px;
  padding: 30px;

  border-radius: 50% !important;
  border: solid rgb(255, 241, 241);
}

:root {
  --swiper-navigation-size: 26px;
}

.swiper-contrainer {
  overflow: visible;
}

.swiper-pagination {
  position: absolute;
  display: block;
  text-align: center;
  transition: 300ms opacity;
  transform: translate3d(0, 0, 0);
  z-index: 2;
  top:97%;
}
.bg-whitee span {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(228, 19, 19);
  transition: 0.5s;
  z-index: -1;
}

.bg-whitee a:hover span {
  width: 100%;
}

