.bg-whitee h2{
  text-transform:capitalize;
  font-size: 20px;
  color: rgb(66, 65, 65);

}

.bg-whitee{
  padding: 1rem;
  background-color: hsl(0, 0%, 100%);
  color: #5c5c5c;
  box-shadow: 5px 5px 5px 5px rgba(255, 0, 0, 0.1);
min-height: 240px;
}


.bg-whitee h4{
  font-weight: 400;
  font-size: 22px;
}

.bg-whitee i{
  font-size: 25px;
  padding-right:  12px;

}


@media (max-width:550px) {
  .bg-whitee h2{
      font-size: 22px;
      width: 93%;
  }
}

.bg-whitee a{
  z-index: 1;
  position: relative;
  color: white;
 font-size: 18px;
 height: 50px;
 max-width: 260px;
}


.bg-whitee span{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: rgb(255, 255, 255);
  transition: 0.5s;
  z-index: -1;
  
}
.bg-whitee a:hover span{
  width: 100%;
  transition: 0.4s;
}

.bg-whitee a:hover{
  color: black;
  border: 2px solid black;
}
.right-arrow{
  border-right: 1.5px solid white;
  
}
.bg-whitee a:hover .right-arrow{
  border-right: 1.5px solid black;
  transition: 0.3s;
}



