.socials{
    position: fixed;
    right: 0;
    top: 45%;
    display: flex;
    flex-direction: column;
    z-index: 10;
  }
  .socials i{
    color: rgba(219, 16, 16, 0.827);
    font-size: 1.5rem;
    padding-bottom: 0.7rem;
    padding-right: 0.5rem;
    
  }
  .socials i:hover{
    color: rgba(228, 19, 19, 0.978);
    transform: scale(1.5,1.5);
  }
  