

.backgroundImgFaqs {
    width: auto;
    height: 100vh;
    object-fit: cover; 
    background-position: center;
    background-image: linear-gradient(rgba(0,0,0,0.7),black),url(../../Components/Assets/q&a.jpeg);
    background-size:cover;
    display: flex;
    align-items: center;
    color: white;
  }

  .question{
    cursor: pointer;
    margin: 2rem;
  }

  
.answer {
    display: none;
    margin-left:1rem;
    font-size: 20px;
  }
  
  .answer.reveal {
    display: block;

  }
  
  .bg{
    width: 95%;
  }
  



  .titles-Faqs{
    margin: 3rem 0rem;
  }
  .titles-Faqs h1{
    position: relative;
    color: rgb(43, 41, 41);
    margin-right: 50px;
  }
  
  .titles-Faqs h1::after{
    background-color: rgba(228, 19, 19, 0.773);
    display: block;
    width: 70%;
    height: 3px;
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    z-index: -1;;
  }
  .titles-Faqs h6{
    color: rgba(228, 19, 19, 0.773);
  }
  
  
  @media (max-width:767px) {
   
    
    .titles-Faqs h1::after{
      width: 48%;
    }

    
    .question{
        margin: 1rem;
    }
   
  }
  
  
  @media (max-width:650px) {
   
    .titles-Faqs h1{
      width: 100%;
    }
    .titles-Faqs h1::after{
      width: 50%;
    }
   
  }
  @media (max-width:550px) {
   
    .titles-Faqs h1{
      width: 100%;
    }
    .titles-Faqs h1::after{
      width: 48%;
    }
  }