.mySwiper1 {
    width: 100%;
    height: 65vh;
    margin-bottom: 3rem;
    overflow: hidden; 
    position: relative;
}

.mySwiper1 img {
    width: 100%;
    height: 100%; 
}

@media (max-width:780px) {
    .mySwiper1{
        width: 100%;
    }
}




.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.5), black), url(../Assets/swiperImg1.jpg);
    object-fit: cover;
    background-size: cover;
    background-position: center;
}
