.breadcrum{
    position: fixed;
    width: 100%;
    top: 73px;
    left: 0rem;
    justify-content: center;
    gap: 8px;
    color: #ffffff;
    background-color: #f0eaea;
    font-size: 16px;
    font-weight: 600;
    text-transform: capitalize;
    z-index: 10;
    padding: 10px 10px;
    padding-left: 5rem;
    transition: 0.5s ;
}
.breadcrum i{
  font-size: 15px;
  padding-right: 4px;
}
.breadcrum a{
  color: white;
}
.breadcrum a:hover{
  border-bottom: 1px solid #5c5c5c;
}

@media (max-width:785px) {
  
    .breadcrum{
      width: 100%;
      padding-left: 2rem;
    }
  }