.backgroundImgSale  {
  width: auto;
  height: 100vh;
  object-fit: cover; 
  background-position: center;
  background-image: linear-gradient(rgba(0,0,0,0.7),black),url(../../Components/Assets/img1.jpg);
  background-size:cover;
  display: flex;
  align-items: center;
  color: white;
}
.backgroundImgRent  {
  width: auto;
  height: 100vh;
  object-fit: cover; 
  background-position: center;
  background-image: linear-gradient(rgba(0,0,0,0.7),black),url(../../Components/Assets/img2.jpg);
  background-size:cover;
  display: flex;
  align-items: center;
  color: white;
}



.cards{
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
}

.titles-property{
  margin: 3rem 1rem;
}
.titles-property h1{
  position: relative;
  color: rgb(43, 41, 41);
  margin-right: 50px;
}

.titles-property h1::after{
  background-color: rgba(228, 19, 19, 0.773);
  display: block;
  width: 70%;
  height: 3px;
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  z-index: -1;;
}
.titles-property h6{
  color: rgba(228, 19, 19, 0.773);
}


@media (max-width:768px) {
 
  
  .titles-property h1::after{
    width: 48%;
  }
 
}


@media (max-width:650px) {
 
  .titles-property h1{
    width: 100%;
  }
  .titles-property h1::after{
    width: 50%;
  }

  
 
}
@media (max-width:550px) {
 
  .titles-property h1{
    width: 100%;
  }
  .titles-property h1::after{
    width: 48%;
  }
  
}

