@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap');

.bg-fixed{
    background: linear-gradient(rgba(0,0,0,0.7),black),url(../Assets/bgfixed.jpg);
    width: 100%;
    height: 85vh;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    margin: 3rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .bg-fixed-desc h1{
    color: rgba(228, 19, 19, 0.773);
    
  }
  .bg-fixed-desc{
    margin: 0 2rem;
    color: white;
    line-height: 2rem;
    font-family: 'Montserrat', sans-serif;
    font-size: 4rem;
    font-family: 'Lato', sans-serif;
  }

  .titles-offers{
    margin: 3rem 1rem;
  }
  .titles-offers h1{
    position: relative;
    color: rgb(43, 41, 41);
    margin-right: 50px;
  }
  
  .titles-offers h1::after{
    background-color: rgba(228, 19, 19, 0.773);
    display: block;
    width: 70%;
    height: 3px;
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    z-index: -1;;
  }
  .titles-offers h6{
    color: rgba(228, 19, 19, 0.773);
  }
  
  
  @media (max-width:768px) {
   
    .titles-offers h1{
      width: 100%;
    }
    .titles-offers h1::after{
      width: 48%;
    }
   
  }
  
  @media (max-width:650px) {
   
    .titles-offers h1{
      width: 100%;
    }
    .titles-offers h1::after{
      width: 50%;
    }
   
  }
  @media (max-width:550px) {
   
    .titles-offers h1{
      width: 100%;
    }
    .titles-offers h1::after{
      width: 50%;
    }

    .about-me p,.about-me h2{
      margin: 0rem -1rem 1rem;
    }
    
  }