*{
    margin: 0;
    padding: 0;
}

.card{
    width: 600px;
    display: flex;
    justify-content: center;
    align-items: center;;
    position: relative;
    margin-bottom: 400px;
    margin-right: auto;
    color: #5c5c5c;
}
.card img:hover{
    transform: scale(1.01);
    transition: 0.4s;

  }


.card img{
    width: 100%;
}

.bg-white{
    position: absolute;
    top: 80%;
    left: 8%;
    z-index: 1;
    width: 98%;
    box-shadow: 1px 3px 5px 0px rgba(0,0,0,0.2);
}

.bg-white h2{
    text-transform:uppercase;

}
.card a{
    z-index: 1;
    position: relative;
    color: white;
   font-size: 18px;
   width: 300px;
  }

  
  .card span{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background-color: rgb(255, 255, 255);
    transition: 0.5s;
    z-index: -1;
    
  }
  .card a:hover span{
    width: 100%;
    transition: 0.4s;
  }

  .card a:hover{
    color: black;
    border: 2px solid black;
  }
  .right-arrow{
    border-right: 1.5px solid white;
    
  }
  .card a:hover .right-arrow{
    border-right: 1.5px solid black;
    transition: 0.3s;
  }

.bg-white{
    padding-top: 3rem;
    padding-left: 3rem;
}

.bg-white h4{
    padding: 1.5rem 0;
    font-weight: 400;
    font-size: 22px;
}

.bg-white i{
    font-size: 25px;
    padding-right:  12px;

}

.read-btn{
    margin: 30px 0;
    display: flex;
    align-items: center;;
    justify-content: space-around;   
}
.read-btn i{
    font-size: 30px;
    padding-right: 20px;
    
}
.read-btn:hover{
    background-color: white;
}

@media (max-width:550px) {
    .bg-white h2{
        font-size: 22px;
        width: 93%;
    }
}

@media (max-width:1400px) {
    .card{
        width: 500px;
    }
}
@media (max-width:1200px) {
    .card{
        width: 430px;
    }
}

@media (max-width:992px) {
    .card{
        width: 92%;
    }
    .card a{
        width: 350px;
    }
}

@media (max-width:650px) {
    .card{
        width: 95%;
    }
    .card a{
        width: 300px;
    }
}


@media (max-width:550px) {
    .card{
        width: 100%;
    }

    .bg-white{
        padding-top: 2rem;
        padding-left: 2rem;
    }
    .read-btn i{
        font-size: 24px;
    }
    .card a{
        font-size: 16px;
        width: 260px;
    }
    
    
}

@media (max-width:450px) {
    .card{
        width: 90%;
    }
    .card a{
        width: 200px;
    }
}

@media (max-width:300px) {
    .card{
        width: 90%;
    }
    .card a{
        width: 170px;
    }
}