:root {
  --bg: rgba(128, 128, 128, 0.253);
  --dark_red: rgba(228, 19, 19, 0.773);
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: transparent;

}

@media(min-width: 992px) {}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  transition: 0.5s ;
}

header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

header ul li {
  display: flex;
  justify-content: space-between;
}



header ul li {
  list-style: none;
  padding-top: 1rem;
  padding-right: 3rem;
}



.nav-close {
  display: none;
  font-size: 2rem;
  color: var(--dark_red);

}

.nav-open {
  display: none;
  font-size: 2rem;
  color: var(--dark_red);
}

.logo img {
  padding-left: 4.5rem;
  height: 60px;
}

.nav-logo,
.nav-toggle {
  color: rgb(0, 0, 0);
}

.nav-logo {
  font-weight: 600;
}

.nav-toggle {
  font-size: 1.2rem;
  cursor: pointer;
  ;
}

.nav-menu,
.nav-menu-full-width {
  position: relative;
}


.nav-link {
  font-weight: 600;
  text-transform: uppercase;
}

.nav-link:hover {
  opacity: 0.9;

}



.nav-close {
  position: absolute;
  top: 0.75rem;
  right: 1rem;
  cursor: pointer;
}



.nav-toggle i {
  font-size: 2.4rem;
}

@media screen and (max-width:910px) {
  

  .nav-menu {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.96);
    top: 0;
    right: -1000%;
    width: 70%;
    height: 100%;
    box-shadow: -1px 0 4px rgba(0, 0, 0, 0.6);
    padding: 3rem;
    transition: 0.4s;
    width: 350px;
    z-index: 11000;
    transition: 0.4s;
    
  }
  .nav-menu.open a {
    color: var(--dark_red);
  }

  .nav-menu.open{
    right: 0;

  }
  .nav-item {
    padding-bottom: 1rem;
    border-bottom: 1px solid white;
    width: 100%;
  }

  .nav-item:hover{
    border-bottom: 1px solid var(--dark_red);
    color: white;
    transition: 0.5s ease-in-out;

  }
  .navbar ul li {
    color: white;
  }

  .nav-open {
    display: block;
    margin-right: 0.5rem;
  }

  .nav-close {
    display: block;
  }

  .nav-list
  {
  display: flex;
  flex-direction: column;
  row-gap: 2.5rem;
}


.logo img {
  height: 50px;
}
}

@media (max-width:785px) {
  
  .logo{
    margin: -3rem;
  }
  
}
@media (max-width:555px) {
  
  .nav-menu.open{
    width: 95%;
  }
  
}