@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600;700;800;900&display=swap');


a{
    text-decoration: none;
    color: black;
}

.background{
    background: linear-gradient(rgba(0,0,0,0.7),black),url(../Assets/carousel2.jpg);
    width: auto;
    height: 100dvh;
    background-size: cover;
    background-position: center;
    color: white;
  }
  .background{
    display: flex;
    align-items: center;
  }
  .bg-description{
  
    animation-delay: 0.5s;
    animation: fadeInUp; 
    animation-duration: 2s; 
    display: flex;
    flex-direction: column;
    
  }
  .bg-description h5{
    margin: 0 5rem;
    color: rgba(228, 19, 19, 0.773);
  font-weight: 700;
  }
  .bg-description h1{
    font-size: 3.6rem;
    font-family: 'Montserrat', sans-serif;
    padding: 1rem 0;
    margin: 0 5rem;
    font-weight: 100;
  
  }
  .bg-description p{
    margin: 0 5rem;
    font-size: large;
    font-family: 'Montserrat', sans-serif;
    line-height: 2rem;
    font-weight: 100;
  }
  .bg-description a{
    text-align: center;
    width: 130px;
    margin-top: 2rem;
    margin-left: 5rem;
    background-color: rgb(255, 255, 255);
    padding: 10px 20px;
    z-index: 1;
    position: relative;
  }
  .bg-description span{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    background-color: rgb(228, 19, 19);
    transition: 0.5s;
    z-index: -1;
  }
  .bg-description a:hover span{
    width: 100%;
  }
  
  .bg-description a:hover{
    color: white;
    transition: 0.3s;
  }


  @media (max-width:1152px) {
    .bg-description h1{
      font-size: 45px;
    }
    }
  @media (max-width:992px) {
    .bg-description h1{
      font-size: 40px;
    }
    }
  @media (max-width:650px) {
    .bg-description h1{
      font-size: 43px;
    }
    }
  
  @media (max-width:500px) {
    .bg-description h1{
      font-size: 30px;
      width: 80%;
      margin:0rem 4rem;
    }
    .bg-description{
      margin: -3rem;
     
    }
    .bg-description p{
      width: 80%;
      margin:1rem 4rem;
    }
    .bg-description a{
      margin: 1rem 4rem;
    }

    .bg-description h5{
      padding:3rem 0;
      margin: 0rem 4rem;
    }
  }