.location{
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid black;
  }
  .location i{
    width: 60px;
    font-size: 2rem;
    padding: 0.9rem;
    border: 1px solid rgba(228, 19, 19, 0.773);
    margin-right: 1rem;
    color: rgba(228, 19, 19, 0.773);
  }
  .location p{
    line-height: 0.5rem;
  }
  
  .email{
    border-bottom: 1px solid black;
  }
  .email a:hover{
    opacity: 0.8;
  }
  .email i{
    margin-bottom: 2rem;
    width: 60px;
    font-size: 2rem;
    padding: 0.8rem;
    border: 1px solid rgba(228, 19, 19, 0.773);
    margin-right: 1rem;
    color: rgba(228, 19, 19, 0.773);
  }
  .number{
    padding-top: 2rem;
  }
  .number i{
    margin-bottom: 2rem;
    width: 60px;
    font-size: 2rem;
    padding: 0.8rem;
    border: 1px solid rgba(228, 19, 19, 0.773);
    margin-right: 1rem;
    color: rgba(228, 19, 19, 0.773);
  }
  
  
  
  #contact{
    margin-top: 2rem;
  }
  
  
  .form-group input{
    width: 100%;
    height: 50px;
    margin-bottom: 0.5rem;
    border: 1px solid black;
    padding: 1.5rem;
    border-radius: 0;
  }
  .form-group textarea{
    width: 100%;
    height: 100px;
    margin-bottom: 0.5rem;
    border: 1px solid black;
    padding: 1.5rem;
    border-radius: 0;
  
  }

  #contact{
    margin-top: 70px;
  }

  .btn-submit{
    border: 1px solid black;
    width: 160px;
    height: 50px;
    padding: 10px 30px;
    margin-bottom: 15px;
    color: rgb(0, 0, 0);
    position: relative;
    transition: 0.5s;
    z-index: 1;
    background-color: white;
  }
  .btn-submit span{
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    z-index: -1;
    color: white;
  }
  .btn-submit:hover{
    color: white;
  }
  .btn-submit:hover span{
    background-color: rgb(0, 0, 0);
    width: 100%;
    transition: 0.4s;
    color: white;
    
  }
.copyright{
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
small a:hover{
  color: yellow;
}